<template>
  <div class="Page2">
    <mu-appbar style="width: 100%;" color="primary">
      <mu-button icon slot="left" @click="iconBack">
        <mu-icon value="arrow_back" ></mu-icon>
      </mu-button>
      工具管理
      <mu-button flat slot="right" @click="iconAdd">
        <mu-icon value="add" size="30"></mu-icon>
      </mu-button>
    </mu-appbar> 

    <div class="mu-text-field mu-input-content" style="margin:10px 5px"> 
         <input placeholder="输入名称可查询" tabindex="0" v-model="search" class="mu-text-field-input my-search-input">
         <mu-button color="red" @click="searchData" style="margin:0 10px 0 0"> 查询 <mu-icon right value="search"></mu-icon> </mu-button>
    </div>

    <mu-dialog title="提示" width="360" :open.sync="openSimple">
     确定删除此记录？
    <mu-button slot="actions" flat color="info" @click="closeSimpleDialog">取消</mu-button>
	  <mu-button slot="actions"   color="primary" @click="buttonDelete">确定</mu-button>
  </mu-dialog>

    <mu-drawer :open.sync="open" :docked="docked" :right="position === 'right'">
     
        <mu-form ref="form" :model="row" >
             
          <mu-form-item label="日期"  > <mu-date-input v-model="row.date" /> </mu-form-item>
          <mu-form-item label="名称"  > <mu-text-field v-model="row.name" /> </mu-form-item>
          <mu-form-item label="规格"  > <mu-text-field v-model="row.guige"/></mu-form-item>
          <mu-form-item label="数量"  > <mu-text-field v-model="row.number" /> </mu-form-item>
          <mu-form-item label="单位"  > <mu-text-field v-model="row.unit" /> </mu-form-item>
          <mu-form-item label="领用人"  > <mu-text-field v-model="row.person"/> </mu-form-item> 
          <mu-form-item> <mu-text-field v-model="row.desc" multi-line :rows="4" icon="comment"  placeholder="备注"></mu-text-field> </mu-form-item>
          <mu-form-item> <mu-button @click="clear">重置</mu-button> <mu-button color="primary" @click="submit">提交</mu-button> </mu-form-item>
        </mu-form>  
    </mu-drawer>
    

    <mu-data-table height="300" :columns="columns" :sort.sync="sort" @sort-change="handleSortChange" :data="list">
      <template slot-scope="scope">
        <td >
          <a @click="deleteLine(scope.row)" class="a-delete">删除</a>
          <a @click="updateLine(scope.row)">修改</a>
        </td>
        <td>{{scope.row.date.substr(0,10)}}</td>
        <td>{{scope.row.name}}</td>
        <td>{{scope.row.guige}}</td>
        <td>{{scope.row.number}}</td>
        <td>{{scope.row.unit}}</td>  
        <td>{{scope.row.person}}</td> 
        <td>{{scope.row.desc}}</td>
      </template>
    </mu-data-table>
    <mu-flex justify-content="center" style="margin: 32px 0;">
        <mu-pagination raised :total="total" :current.sync="current" @change="change"></mu-pagination>
    </mu-flex>
  </div>
</template>

<script>
import {getExcelJosn,updateExcel,deleteExcel,addOneExcel} from '@/action/index'
const table = 'tb';
const pageSize = 13;
export default {
  name: 'Home',
  data () {
    return {
        docked: false,
        open: false,
        position: 'left', 
        openSimple:false, 
         usernameRules: [
        { validate: (val) => !!val, message: '必须填写用户名'},
        { validate: (val) => val.length >= 3, message: '用户名长度大于3'}
      ],
      passwordRules: [ 
      ],
      argeeRules: [{ validate: (val) => !!val, message: '必须同意用户协议'}],
      /* TABLE */
      row:{
        date: "2020-11-10T16:00:00.000Z",
desc: "",
guige: "把",
id: 704492,
name: "榔头",
number: "1",
person: "",
unit: "wzskyline",
      },
      sort: { name: '', order: 'asc' },
      columns: [ 
          { title: '操作',  name: 'id' },
          { title: '日期',  name: 'date' },
          { title: '名称', name: 'name',  },
          { title: '规格', name: 'guige',  },
          { title: '数量', name: 'number',  },
          { title: '单位', name: 'unit',  }, 
          { title: '领用人', name: 'person',  }, 
          { title: '备注', name: 'desc',  },
      ],
      list: [  ],
      current:1,
      total:100,
      search:'',
    }
  },
  created(){
   console.log('created()')
  },
  mounted(){
    this.getList({table ,search:'',page:1,pageSize:pageSize})
  },
  methods: {
    searchData(){
     this.list =[]
     this.getList({table ,search:this.search,page:1,pageSize:pageSize})
    },
    getList(arg){ var _this = this;
       getExcelJosn(arg).then(res=>{
          _this.list = res.data
          _this.total = res.count
          _this.current = parseInt(res.page)
       }) 
    },
    change(page){
     console.log(page)
     this.list =[]
     this.getList({table ,search:this.search,page,pageSize:pageSize})
    },
    deleteLine(row){
     this.openSimple = true;
     this.row =row;
     console.log(row)
    },
    updateLine(row){ 
     this.row = row; 
     this.open = true;
     var tmp = new Date(this.row.date) 
     this.row.date = `${tmp.getFullYear()}-${tmp.getMonth()+1}-${tmp.getDate()-1}`
     console.log(row)
    },
    handleSortChange ({name, order}) {
      this.list = this.list.sort((a, b) => order === 'asc' ? a[name] - b[name] : b[name] - a[name]);
    },
    openSimpleDialog () {
      this.openSimple = true;
    },
    closeSimpleDialog () {
      this.openSimple = false;
    },
    buttonDelete(){
       var _this = this; 
            deleteExcel(table,this.row.id).then(res=>{
                 _this.openSimple = false;
               _this.flush();
               res?this.$toast.success('删除成功'):this.$toast.error('删除失败');
         })
    },
    iconAdd(){
      this.open = true;
      this.row.id = '';
      this.row.date = new Date();
      this.row.number = 10;
    },
    iconBack(){
      this.$router.push({name: '/',params:{}});
    },
    flush(){
        this.open = false;
        this.getList({table ,search:'',page:this.current,pageSize:pageSize})
    },
    submit () {
       var _this = this; 
       _this.open = false;
       console.log(this.row.date)
       var tmp = new Date(this.row.date) 

       this.row.date = `${tmp.getFullYear()}-${tmp.getMonth()+1}-${tmp.getDate()+1}`
       try {
           if(this.row.id){
             updateExcel(table,this.row).then(res=>{ 
               _this.flush();
               res?this.$toast.success('更新成功'):this.$toast.error('更新失败');
             })
           }else{
            addOneExcel(table,this.row).then(res=>{
              _this.flush();
              res?this.$toast.success('添加成功'):this.$toast.error('添加失败');
            })
           } 
       } catch (error) {
          this.$toast.error('操作失败,请检查网络');
       }
    },
    clear () {
      this.$refs.form.clear();
      this.row = {
        date: "",
        desc: "",
        guige: "",
        name: "",
        number: "",
        person: "",
        unit: "",
      };
    }
  }
}
</script>
  
<style scoped>
 
</style>
